<!--  -->
<template>
  <div class="loginContainer">
    <div class="loginInner">
      <div class="login_header">
        <h2 class="login_logo"></h2>
        <div class="login_header_title">
          <a href="javascript:;" :class="{on:loginWay}" @click="loginWay=true"></a>
          <a href="javascript:;" :class="{on:!loginWay}" @click="loginWay=false"></a>
        </div>
      </div>
      <div class="login_content">
        <form @submit.prevent="login">
          <div :class="{on:loginWay}">
            <section class="login_message">
              <input type="tel" maxlength="11" placeholder="" v-model="phone">
              <button :disabled="!rightPhone" class="get_verification" 
                      :class="{right_phone:rightPhone}" @click.prevent="getCode">
                {{computeTime ? `(${computeTime})s` : ""}}</button>
            </section>
            <section class="login_verification">
              <input type="text" maxlength="8" placeholder="" v-model="code">
            </section>
            <section class="login_hint">
              
              <a href="javascript:;"></a>
            </section>
          </div>
          <div :class="{on:!loginWay}">
            <section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="//" v-model="name">
              </section>
              <section class="login_verification">
                <input type="text" maxlength="8" placeholder="" v-if="showPwd" v-model="pwd">
                <input type="password" maxlength="8" placeholder="" v-else v-model="pwd">
                <div class="switch_button" :class="showPwd?'on':'off'" @click="showPwd=!showPwd">
                  <div class="switch_circle" :class="{right:showPwd}"></div>
                  <span class="switch_text">{{showPwd?'abc':''}}</span>
                </div>
              </section>
              <section class="login_message">
                <input type="text" maxlength="11" placeholder="" v-model="captcha">
                <img class="get_verification" src="/api/captcha" alt="captcha"
                 @click="getCaptcha" ref='captcha'>
              </section>
            </section>
          </div>
          <button class="login_submit"></button>
        </form>
        <a href="javascript:;" class="about_us"></a>
      </div>
      <span href="javascript:" class="go_back" @click="$router.back()">
        <i class="iconfont icon-jiantou2"></i>
      </span>
    </div>
    <AlertTip :alertText='alertText' v-show="alertShow" @closeTip="closeTip"/>
  </div>
</template>

<script>
import AlertTip from '../../components/AlertTip/AlertTip.vue'
import {reqSendCode,reqSmsLogin,reqPwdLogin} from '../../api'

export default {
  data() {
    return {
      
      loginWay:true,//truefalse
      computeTime:0,//
      showPwd:false,//
      pwd:'',//
      code:'',//
      captcha:'',//
      name:'',//
      phone:'',//
      alertText:'',//
      alertShow:false,//
    }
  },
  computed: {
    rightPhone(){
      return /^1\d{10}$/.test(this.phone)
    }
  },
  methods: {
    //
    async getCode(){
      //
      if(!this.computeTime){
         //
      this.computeTime = 30
      this.intervalId = setInterval(()=>{
        this.computeTime--
        if(this.computeTime<=0){
          //
          clearInterval(this.intervalId)
        }
      },1000)

       //ajax
       const result = await reqSendCode(this.phone)
       if(result.code===1){
         //
        this.showAlert(result.msg)
         //
         if(this.computeTime){
           this.computeTime=0
           clearInterval(this.intervalId)
           this.intervalId=undefined
         }
       }
      }
    },
    //
    showAlert(alertText){
        this.alertShow = true
        this.alertText = alertText
    },
    //
    async login(){
      //
      let result
      if(this.loginWay){//
        const {rightPhone,phone,code} = this
        if(!this.rightPhone){
          //
          this.showAlert("")
          return
        }else if(!/^\d{6}$/.test(code)){
          //6
          this.showAlert("6")
          return
        }
        //ajax
        result = await reqSmsLogin(phone,code)

      }else{//
        const {name,pwd,captcha} = this
        if(!this.name){
          //
          this.showAlert("")
          return
        }else if(!this.pwd){
          //
          this.showAlert("")
          return
        }else if(!this.captcha){
          //
          this.showAlert("")
          return
        }
        //ajax
        result = await reqPwdLogin({name,pwd,captcha})
        
      }
      //()
         if(this.computeTime){
           this.computeTime=0
           clearInterval(this.intervalId)
           this.intervalId=undefined
         }
      //
      if(result.code===0){
            const user = result.data
            //uservuexstate
            this.$store.dispatch('recordUser',user)
            //
            this.$router.replace('/profile')
        }else{
            this.name=''
            this.pwd=''
            this.captcha=''
            this.phone=''
            this.code=''
            const msg = result.msg
            this.getCaptcha()//
            this.showAlert(msg)  
        }
    },
    //
    closeTip(){
      this.alertShow = false
      this.alertText = ''
    },
    //
    getCaptcha(){//event
    // let BASE_URL=process.env.VUE_APP_API_URL||'/api'
      //src
      this.$refs.captcha.src=`/api/captcha?time=`+Date.now()
    }
    },
  components:{
    AlertTip
  }
}

</script>

<style lang='stylus' rel='styledheet/stylus'>
  @import "../../common/stylus/mixins.styl"
  .loginContainer
    width 100%
    height 100%
    background #fff
    .loginInner
      padding-top 60px
      width 80%
      margin 0 auto
      .login_header
        .login_logo
          font-size 40px
          font-weight bold
          color #02a774
          text-align center
        .login_header_title
          padding-top 40px
          text-align center
          >a
            color #333
            font-size 14px
            padding-bottom 4px
            &:first-child
              margin-right 40px
            &.on
              color #02a774
              font-weight 700
              border-bottom 2px solid #02a774
      .login_content
        >form
          >div
            display none
            &.on
              display block
            input
              width 100%
              height 100%
              padding-left 10px
              box-sizing border-box
              border 1px solid #ddd
              border-radius 4px
              outline 0
              font 400 14px Arial
              &:focus
                border 1px solid #02a774
            .login_message
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .get_verification
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                border 0
                color #ccc
                font-size 14px
                background transparent
                &.right_phone
                  color black
            .login_verification
              position relative
              margin-top 16px
              height 48px
              font-size 14px
              background #fff
              .switch_button
                font-size 12px
                border 1px solid #ddd
                border-radius 8px
                transition background-color .3s,border-color .3s
                padding 0 6px
                width 30px
                height 16px
                line-height 16px
                color #fff
                position absolute
                top 50%
                right 10px
                transform translateY(-50%)
                &.off
                  background #fff
                  .switch_text
                    float right
                    color #ddd
                &.on
                  background #02a774
                >.switch_circle
                //transform translateX(27px)
                  position absolute
                  top -1px
                  left -1px
                  width 16px
                  height 16px
                  border 1px solid #ddd
                  border-radius 50%
                  background #fff
                  box-shadow 0 2px 4px 0 rgba(0,0,0,.1)
                  transition transform .3s
                  &.right
                    transform translateX(30px)
            .login_hint
              margin-top 12px
              color #999
              font-size 14px
              line-height 20px
              >a
                color #02a774
          .login_submit
            display block
            width 100%
            height 42px
            margin-top 30px
            border-radius 4px
            background #4cd96f
            color #fff
            text-align center
            font-size 16px
            line-height 42px
            border 0
        .about_us
          display block
          font-size 12px
          margin-top 20px
          text-align center
          color #999
      .go_back
        position absolute
        top 5px
        left 5px
        width 30px
        height 30px
        >.iconfont
          font-size 20px
          color #999
</style>



